import React from "react";
import mainimg from "./assets/save.svg"
import Bars from "./components/BardsCharts";
import Claster from "./components/Claster";
import sbeimg from "./assets/sueldo-basico-ecuador-2023.webp"



 

const TextContent = ()=>{
    return (
      
            <div className="texts">
                <img src={mainimg}  alt="sbuEcuador" width={'180px'}/>

                <header>
		<nav className="TableContents">
			<ol>
				<li><a href="#que-es">¿Qué es el salario básico unificado?</a></li>
				<li><a href="#sueldo-2023"> <strong>Sueldo básico Ecuador</strong> 2023</a></li>
				<li><a href="#impacto">Impacto del sueldo básico en tu economía</a></li>
				<li><a href="#canasta-basica">Canasta básica Ecuador</a></li>
			</ol>
		</nav>
	</header>
	<main className="homeContent">
		<section id="que-es">
			<header>
				<h1>¿Qué es el salario básico unificado?</h1>
			</header>
			<p>El salario básico unificado es el monto mínimo que el empleador debe pagar a un trabajador por sus servicios en el Ecuador. Es establecido por el gobierno cada año y se aplica a todos los trabajadores, independientemente del sector en el que trabajen.</p>
		</section>
		<section id="sueldo-2023">
			<header>
				<h1>Sueldo básico Ecuador 2023</h1>
			</header>
			<p>El sueldo básico Ecuador 2023 ya ha sido establecido por el gobierno del Ecuador. Sin embargo, se espera que el monto sea ajustado de acuerdo a la inflación y el costo de vida en el país. Los trabajadores estarán pendientes de este anuncio para conocer cómo afectará a sus ingresos y gastos mensuales.</p>
			<p>Es importante tener en cuenta que el salario básico unificado no es lo mismo que el salario total que recibe un trabajador. El sueldo básico es solo una parte del pago total, y los trabajadores pueden recibir bonificaciones,horas extray otros beneficios adicionales.</p>
		</section>
		<div className="imgMain"><img src={sbeimg} alt="sueldo-basico-ecuador-2023" /></div>
			<div>
			<Bars></Bars>

			</div>
  <div>
  <h1>Este es el contenido principal</h1>
  <div className="claster-section">
    <Claster />
  </div>
</div>

		<section id="impacto">
			<header>
				<h1>Impacto del sueldo básico en tu economía</h1>
			</header>
			<p>El sueldo básico unificado es una referencia importante para establecer los salarios de los trabajadores en el país, pero también tiene un impacto en la economía personal de cada trabajador. Si el sueldo básico se incrementa, los trabajadores pueden esperar un aumento en sus ingresos, pero también pueden enfrentar un aumento en el costo de vida, especialmente en cuanto a los bienes y servicios básicos.</p>
			<p>Es importante estar al tanto de los cambios en el salario básico unificado y planificar su presupuesto en consecuencia para evitar dificultades financieras.</p>
		</section>
 
		<section id="canasta-basica">
			<header>
				<h1>Canasta básica Ecuador</h1>
			</header>
			<p>La canasta básica es el conjunto de bienes y servicios que una persona necesita para cubrir sus necesidades básicas. El costo de la canasta básica en Ecuador también se actualiza anualmente y se utiliza como referencia para medir la pobreza en el país. Según el Instituto Nacional de Estadística y Censos (INEC), en febrero de 2023, el costo de la canasta básica en Ecuador fue de $600 por mes para una familia de cuatro personas.</p>
<p>Este costo puede variar dependiendo de la región en la que se encuentre y de los cambios en el salario básico unificado. Es importante tener en cuenta el costo de la canasta básica al planificar un presupuesto y establecer el sueldo que se necesita para cubrir las necesidades básicas de la familia.</p>
<p>En conclusión, el salario básico unificado es un tema importante para los trabajadores en Ecuador, ya que afecta directamente sus ingresos y gastos mensuales. Estar informado sobre los cambios en el salario básico y el costo de la canasta básica puede ayudar a planificar un presupuesto y evitar dificultades financieras.</p>
        </section>
</main>

            </div>
    )
}

export default TextContent