import React from "react";
import "./Claster.css";

class Claster extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      articles: [
        {
          id: 1,
          title: "Horas Extras",
          summary: "Horas Extras",
        },
        {
          id: 2,
          title: "Beneficios de ley",
          summary: "Beneficios de ley",
        },
        {
          id: 3,
          title: "Políticas laborales",
          summary: "Politicas laborales",
        },
        
    
 
      ],
    };
  }

  render() {
    return (
      <div className="grid-container">
        {this.state.articles.map((article) => (
          <div className="article" key={article.id}>
            <h2>{article.title}</h2>
            <p>{article.summary}</p>
            <a href="https://www.trabajo.gob.ec/" target="_blank">Leer más</a>
          </div>

        ))}
      </div>
    );
  }
}

export default Claster;
