import { Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  Filler,
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  Filler
);

const beneficios = [375, 386, 394, 400, 400, 425, 450];
const Años = ["2017","2018", "2019", "2020", "2021", "2022", "2023"];

const misoptions = {
  responsive: true,
  plugins: {
    legend: {
      display: false
    }
  },
  scales: {
    x: {
      ticks: {
        color: '#094f6e',
        font: {
          size: 20
        }
      }
    },
    y: {
      ticks: {
        beginAtZero: true,
        font: {
          size: 22
        }
      }
    }
  }
};

const midata = {
  labels: Años,
  datasets: [
    {
      label: 'SBU',
      data: beneficios,
      backgroundColor: '#094f6e'
    }
  ]
};

export default function Bars() {
  return (
    <div style={{ height: '400px', width: '100%', display: 'flex', justifyContent: 'center' }}>
      <Bar data={midata} options={misoptions} />
    </div>
  )
}
